.scheduled-quotes-cont {
	display: grid;
	width: 99%;
	grid-template-columns: 50% 50%;
	align-items: center;
	justify-content: center;
	overflow-y: hidden;
	min-height: max-content !important;
	height: 90%;
}

.date-view {
	width: 100%;
	height: 100%;
	max-height: 100%;
	min-height: 100%;
}

#last-week>.week-view {
	border-top-left-radius: 0px;
	border-top-right-radius: 0px;
	border-bottom-left-radius: 0px;
	max-height: calc(100% - 40px);
	min-height: calc(100% - 40px);
}
.week-view>.card-content {
	overflow-x: hidden;
}
#last-week>.week-view>.card-content {
	
}
#last-week>.week-view>.card-content>:last-child {
	border-bottom: 0px solid #ccc;
}
#last-week>.week-view>.card-content>:first-child {
	border-bottom: 1px solid #ccc;
}
#this-week>.week-view {
	border-top-right-radius: 0px;
	border-bottom-right-radius: 0px;
	border-top-left-radius: 0px;
	border-right: 0px;
	max-height: calc(100% - 40px);
	min-height: calc(100% - 40px);
}

#this-week>.week-view>.card-content>:first-child  {
	background-color: #f0f0f0;
}
#this-week>.week-view>.card-content>:first-child:hover {
	border-bottom: 1px solid #43bc90;
}

.date-view>.card-menubar {
	width: 100%;
	position: relative;
}
#this-week>.card-menubar {
	border-top-right-radius: 0px;
}
#last-week>.card-menubar {
	border-top-left-radius: 0px;
}
/*Legend*/
.chart-legend {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 10px;
	width: fit-content;
}
.legend-item {
	display: flex;
	gap: 10px;
	font-size: large;
	font-style: italic;
	font-weight: 600;
	width: fit-content;
	align-items: center;
	justify-content: center;
	padding: 10px;
}
.legend-color {
	background-color: #43bc90;
	width: 7.5px;
	height: 20px;
	border-radius: 2.5px;
}
.legend-color#quote {
	background-color: #588cdb;
}
.legend-color#job {
	background-color: #43bc90;
}

.quote-table-container {
	height: 100%;
}

@media screen and (min-width: 1768px) {
	.scheduled-quotes-cont {
		height: fit-content;
	}
}
