/*WEEK VIEW*/
.day-selection {
	width: 50%;
	padding: 10px;
	display: grid;
	grid-template-columns: 30% 70%;
	align-items: center;
	justify-content: center;
}
.date-view {
	display: flex;
	flex-direction: column;
	padding: 0px;
	width: 95%;
	min-height: 100%;
	max-height: 100%;
}
.week-view {
	min-height: 90%;
	max-height: 90%;
	width: 100%;
	overflow: hidden;
}
.week-view>.card-content {
	display: flex;
	flex-direction: column;
	grid-template-rows: repeat(8, 12.5%) !important;
	padding: 0px;
	height: 100%;
	overflow-y: auto !important;
}
.day-card {
	display: grid;
	height: fit-content !important;
	grid-template-columns: 80% 20%;
	border-bottom: 1px solid #ccc;
}
.day-card:hover {
	background-color: #f6f6f6;
}
.week-view>.card-content>:last-child {
	border-bottom: 0px;
}
.day-card.today {
	background-color: #f0f0f0;
}
.day-card.today:hover {
	border-bottom: 1px solid #43bc90;
}
.day-info {
	border-right: 1px solid #ccc;
	display: flex;
	flex-direction: row;
	flex-wrap:wrap-reverse;
	align-items: center;
	justify-content: start;
	gap: 10px;
	row-gap: 0px;
	overflow-y: scroll;
	padding-left: 10px;
	padding-right: 10px;
}
.day-label {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}
.day-label>.weekday {
	font-size: 20px;
	font-weight: 350;
	color: #424242;
	font-style: italic;
}
.day-label>.date {
	font-size: 40px;
	font-weight: 500;
	color: #212121;
}
.today>.day-label>.weekday {
	color: #212121;
	font-weight: 400;
}
.today>.day-label>.date {
	color: #000;
	font-weight: 650;
}

.day-card-divider {
	width: 100%;
	display: grid;
	grid-template-columns: 20% 20% 60%;
	align-items: center;
	border-bottom: 1px solid #ccc;
	justify-content: center;
}
.day-card-divider:hover {
	background-color: #f0f0f0;
}
.day-card-divider>.month {
	font-size: 30px;
	color: #323232;
}
.day-card-divider>.divider {
	height: 1px;
	width: 100%;
	background-color: #ccc;
}

.chip {
	font-weight: 600;
	border-radius: 5px;
	width: 100px;
	height: 65px;
	padding: 5px;
	overflow: hidden;
	text-overflow: ellipsis;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}
.chip-street {
	width: 80px;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}
.chip.clickable:hover {
	cursor: pointer;
}
