.filter-header>div {
	font-weight: 400;
	font-size: 18px;
}

.filter-button {
	filter: unset;
	text-align: center;
	width: max-content;
	line-height: 100%;
	color: #212121;
	padding: 5px;
	background-color: rgb(160, 204, 198);
	border-radius: 15px;
	padding-left: 10px;
	padding-right: 10px;
	border: 1.5px solid rgb(118, 172, 164);
	cursor: pointer;
	display: flex;
	gap: 10px;
	text-transform: capitalize;
}

.filter-button.selected {
	background-color: rgb(118, 172, 164);
	flex-direction: row-reverse;
}
.filter-button.selected>img {
	height: 10px;
	width: 10px;
	align-self: center;
}

.filter-selected-x {
	/*efefef*/
}
.filter-button-img {
	height: 10px;
}
.filter-selected-x:hover {
	font-weight: 800;
}

.filter-button:hover {
	background-color: rgb(118, 172, 164);
	color: #212121;
}

/*Quote Preview Card*/
.quote-preview-card {
	width: max-content;
	filter: unset;
	border: 1px solid #ccc;
	overflow: hidden;
}
.quote-preview-card>.card-content {
	padding: 0px;
	height: 100%;
	display: flex;
	flex-direction: column;
}

.quote-info {
	width: 100%;
	margin: 5px;
	display: flex;
	flex-direction: column;
	align-items: start;
	justify-content: start;
	height: 70%;
}

.quote-info-title-min {
	color: rgb(80, 80, 80);
	font-style: italic;
	font-weight: 550;
	overflow: hidden;
}

.quote-info>.quote-info-title {
	font-size: 30px;
	font-weight: 700;
	color: rgb(60, 60, 60);
	margin-top: 0px;
	padding-top: 0px;
	overflow: hidden;
	display: -webkit-box;
	-webkit-line-clamp: 1; /* number of lines to show */
			line-clamp: 1; 
	-webkit-box-orient: vertical;
	width: 100%;
	text-align: left;
}

.quote-info-date {
	font-style: italic;
	color: rgb(60, 60, 60);
	text-align: left;
}

.quote-info-progress {
	font-style: italic;
	font-weight: 550;
	color: rgb(50, 50, 50);
}
.quote-info-progress {
	text-transform: capitalize;
}

.quote-actions {
	background-color: #f1f1f1;
	display: flex;
	width: 100%;
	justify-content: center;
	align-items: center;
	grid-template-columns: 40% 40%;
	gap: 5%;
	padding-top: 10px;
	padding-bottom: 10px;
	height: 30%;
}

.quote-actions>.action-button {
	width: 40%;
	height: fit-content;
	font-size: medium;
	padding: 5px;
}

/*Containers*/
.quote-cards-container.open {
	display: grid;
	grid-template-columns: repeat(3, 32.5%);
	height: fit-content;
	max-height: 100%;
	overflow-y: scroll;
	overflow-x: hidden;
	gap: 10px;
}
.quote-cards-container.open>.quote-preview-card {
	height: min-content;
	height: 100%;
	width: 100%;
	min-height: unset;
}

/*QUOTE DETAILED VIEW CARD*/
#quote-card {
	width: 50vw;
	min-height: max-content;
	max-height: 95vh;
}
#quote-card>.card-content {
	display: flex;
	flex-direction: column;
	gap: 5px;
	height: calc(100% - 40px);
}
.quote-card-header {
	display: flex;
	flex-direction: column;
	height: 15%;
}
.quote-made-date {
	font-style: italic;
	color: rgb(60, 60, 60);
	align-self: start;
}
.quote-job-name>.header-text {
	font-size: 25px;
	text-align: left;
}
.quote-info-status {
	font-style: italic;
	font-weight: 550;
	color: rgb(50, 50, 50);
}

/*Quote Creator Card*/
#create-quote-card-card-content {
	height: calc(100% - 40px);
	display: flex;
	flex-direction: column;
	gap: 1.5%;
	overflow-y: scroll;
}

#create-quote-card {
	width: 70vw;
	max-height: 90vh;
	min-height: fit-content;
}

.quote-creator-form-item {
	padding: 10px;
	display: grid;
	grid-template-columns: 30% 65%;
	gap:5%;
	align-items: center;
}
.quote-creator-form-item>input {
	width: 100%;
}
.quote-creator-form-item>.form-info-title {
	font-size: 25px;
}