#submitted-card-card-content {
	padding: 0px;
	height: calc(100% - 40px);
}

.sub-table-cont {
	height: calc(100% - 40px);
	overflow-y: auto;
}

.submitted-quote-tablerow,
.sub-table-cont>.tablerow {
	height: fit-content;
	display: grid;
	grid-template-columns: repeat(3, 33%);
	border-bottom: 1px solid #ccc;
	align-items: center;
	justify-content: center;
	cursor: pointer;
}

.submitted-quote-tablerow>div,
.sub-table-cont>.tablerow>div {
	margin: 0px;
	padding: 0px;
	background-color: transparent;
	overflow: hidden;
}

.submitted-quote-tablerow:hover,
.sub-table-cont>.tablerow:hover {
	background-color: #fafafa;
}

.tablerow.submitted-quote-tablerow.header {
	background-color: #f0f0f0;
	padding: 0px;
	margin: 0px;
}
.tablerow.submitted-quote-tablerow.header>div {
	background-color: transparent;
	padding: 10px;
	margin: 0px;
	border-right: 1px solid #ccc;
	font-weight: 600;
}
.tablerow.submitted-quote-tablerow.header>:last-child {
	border-right: 0px;
}